<template>
  <section>
    <h1>Upload new File!</h1>
    <p>Select the file you want to send and the consumer whose will receive the file</p>

    <form
      enctype="multipart/form-data"
      method="POST"
      @submit.prevent
      @submit.capture="checkCheckBoxes()"
      @submit="submitForm()"
    >
      <label for="fileField">
        <img src="download_button.png" alt="download_button" />
        <span>Click to add File</span>
        <input type="file" name="fileToUpload" id="fileField" @change="checkFile();" required />
      </label>
      <label for="filenameField">Enter the file name</label>
      <input
        type="text"
        id="filenameField"
        name="filename"
        v-model="file.name"
        placeholder="Enter filename"
      />
      <input type="submit" value="SEND" />

      <table>
        <colgroup>
          <col />
          <col />
        </colgroup>

        <tr>
          <th>Consumer</th>
          <th>Select</th>
        </tr>
        <tr v-for="consumer in consumers" v-bind:key="consumer.username">
          <td>{{consumer.username}}</td>
          <td>
            <input
              type="checkbox"
              v-bind:name="consumer.username"
              v-model="consumer.checked"
              v-bind:value="consumer.username"
            />
          </td>
        </tr>
      </table>

      <h2>Add hashtags</h2>
      <div>
        <input
          type="text"
          v-model="newHashtag"
          v-on:keydown.enter.prevent
          v-on:keyup.enter="addHashtag()"
          placeholder="Add Hashtag"
        />
        <button type="button" @click="addHashtag">
          <img src="add_button.png" alt="add_button" />
        </button>
      </div>

      <ul>
        <li v-for="hashtag in file.hashtags" :key="hashtag">
          <Hashtag class="hashtags" v-bind:hashtag="hashtag" @delete-hashtags="deleteHashtags" />
        </li>
      </ul>
    </form>
  </section>
</template>

<script>
import Axios from "axios";
import Hashtag from "../components/Hashtag";

export default {
  name: "AddFile",
  components: {
    Hashtag,
  },

  data: function () {
    return {
      file: {
        name: "",
        hashtags: [],
        content: "",
      },
      newHashtag: "",
      consumers: [],
    };
  },

  watch: {
    "file.name": function () {
      this.file.name = this.file.name.replace(" ", "_");
    },
    newHashtag: function () {
      this.newHashtag = this.newHashtag.replace(" ", "_");
    },
  },

  methods: {
    checkFile: function () {
      //get the input file element
      let fileField = document.getElementById("fileField");
      //get the label of the input file element
      let fileFieldLabel = document.querySelector("label[for='fileField']");

      //check if the user have chosen a file
      if (fileField.value === "") {
        //change background color to white if the user has removed the file
        fileFieldLabel.style.backgroundColor = "white";
        //set the input file name to empty string
        this.file.name = "";
      } else {
        //set background color to black if the user select a file
        fileFieldLabel.style.backgroundColor = "rgb(65, 65, 65)";
        //get the start index of the filename
        let startIndex = fileField.value.lastIndexOf("\\") + 1;
        this.file.name = fileField.value.substring(startIndex);

        //get binary data as base64
        let fileReader = new FileReader();
        fileReader.readAsDataURL(
          document.querySelector("input[type='file']").files[0]
        );
        let that = this;
        fileReader.onload = function () {
          let startIndex = fileReader.result.indexOf(",") + 1;
          that.file.content = fileReader.result.substring(startIndex);
        };
      }
    },

    addHashtag: function () {
      if (this.newHashtag != "") {
        this.file.hashtags.push(this.newHashtag);
        this.newHashtag = "";
      } else {
        event.currentTarget.parentElement.parentElement.querySelector(
          "input[type='text']"
        ).style.color = "grey";
        event.target.parentElement.parentElement.querySelector(
          "input[type='text']"
        ).style.borderColor = "red";
        event.target.parentElement.parentElement.querySelector(
          "input[type='text']"
        ).placeholder = "devi inserire un hashtag";
      }
    },

    deleteHashtags: function (hashtag) {
      this.file.hashtags = this.file.hashtags.filter(
        (oldHashtag) => oldHashtag !== hashtag
      );
    },

    checkCheckBoxes: function () {
      let check = false;
      this.consumers.forEach((element) => {
        if (element.checked === true) {
          check = true;
        }
      });
      if (check === false) {
        alert("You have to select at least one consumer");
        event.preventDefault();
      }
    },

    submitForm: function () {
      let queryString = this.consumers
        .filter((element) => element.checked === true)
        .map((element) => {
          return element["username"];
        })
        .join("+");

      Axios.post(
        `${process.env.VUE_APP_APIROOT}uploaderarea/filemanagment?consumers=${queryString}`,
        this.file
      )
        .then((response) =>
          console.log(`Il server ha risposto con: ${response.status}`)
        )
        .catch((error) => console.log(error));
    },
  },

  created: function () {
    Axios.get(`${process.env.VUE_APP_APIROOT}uploaderarea/consumermanagment`)
      .then((response) => {
        let array = response.data.map((element) => ({
          username: element.username,
        }));
        array.forEach((element) => (element.checked = false));
        this.consumers = array;
      })
      .catch(() => alert("Non sono riuscito a caricare i consumers associati"));
  },
};
</script>

<style scoped>
h1 {
  text-align: center;
  font-size: 3em;
}

form {
  margin-right: auto;
  margin-left: auto;
  width: 80%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: stretch;
  height: 65vh;
  /* border: 2px solid red; */
}

form label[for="fileField"] {
  background-color: white;
  border-style: dashed;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 15px;
  width: 30%;
  text-align: center;
  font-size: 100%;
  flex-basis: 40%;
  /* flex-shrink:; */
}

form label[for="fileField"]:hover {
  cursor: pointer;
  background-color: rgb(65, 65, 65);
  border-style: solid;
}

form label img {
  width: 50%;
  height: auto;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 5%;
  margin-bottom: 3%;
}

form label span {
  display: block;
  margin-bottom: 2%;
  color: rgb(104, 104, 104);
}

form label[for="filenameField"] {
  margin-right: auto;
  margin-left: auto;
  width: 40%;
}

form label input[type="file"] {
  display: none;
}

form input[type="text"] {
  margin-top: 1.5%;
  margin-bottom: 1.5%;
  margin-left: auto;
  margin-right: auto;
  flex-basis: 10%;
  width: 40%;
}

form input[type="submit"] {
  margin-right: auto;
  margin-left: auto;
  width: 40%;
  flex-basis: 15%;
  border-radius: 5px;
  background-color: grey;
  font-weight: bolder;
  font-size: 100%;
  border: 2px solid black;
}

form input[type="submit"]:hover {
  cursor: pointer;
  background-color: rgba(128, 128, 128, 0.5);
}

button [type="submit"] {
  width: 40%;
  margin-right: auto;
  margin-left: auto;
}

table {
  width: 30%;
  margin-right: auto;
  margin-left: auto;
  text-align: center;
  max-height: 20vh;
  overflow: auto;
  display: block;
}

table colgroup col:first-child {
  width: 10vw;
}

table colgroup col:last-child {
  width: 5vw;
}

form div {
  flex-basis: 12%;
  max-height: 38px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  width: 40%;
}

form div input[type="text"] {
  box-sizing: border-box;
  flex-basis: 90%;
  height: 100%;
  margin: 0;
  padding: 0;
  border: 1px solid grey;
  border-radius: 3px;
}

form div button[type="button"] {
  box-sizing: border-box;
  flex-basis: 10%;
  height: 100%;
  padding: 0px;
  margin: 0px;
}

div button[type="button"] img {
  width: 98%;
  height: auto;
  vertical-align: middle;
}

form ul {
  flex-basis: 40%;
  margin: 0 auto;
  border: 0;
  padding: 0;
  width: 40%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

form ul li {
  list-style: none;
  flex-grow: 0;
}

::-webkit-scrollbar {
  display: none;
}
</style>